import React, { Component } from 'react';
import HomeImage from '../images/home_img.jpg'
import Homeanim from '../images/home_anim.gif'
import './Home.css'

class Home extends Component {
    render() {
        return (
            <>
            <div className="home-img">
                <img src={HomeImage} alt="home_img" />
                <div className="home-anim">
                    <div className="home-anim-img">
                        <img src={Homeanim} alt="anim" />
                        <div className="anim-text">
                            <p>helo</p>

                        </div>
                    </div>
                </div>
                
                
            </div>
            </>
        );
    }
}

export default Home;