
import 'bootstrap/dist/css/bootstrap.min.css' 
import './App.css'
import React, { Component } from "react"; 
import {BrowserRouter as Router,Link,Route,Redirect} from 'react-router-dom'
import Home from './components/Home'
import Characters from './components/Characters.js'


class App extends Component {
  render() {
    return (
     <Router>

        <React.Fragment>
        <nav className="navbar navbar-expand-sm navbar-dark bg-dark">
          <div className="container-fluid">
            <Link className="navbar-brand" to={'/'}>wizardingworld</Link>
           
            <div className=" navbar-collapse" >
              <ul className="nav navbar-nav me-auto mb-2 mb-lg-0">
                <li className="nav-item">
                  <Link className="nav-link" to={'/'}>Home</Link>
                </li>
                <li className="nav-item">
                  <Link className="nav-link"to={'/characters'}>Characters</Link>
                </li>
                <li className="nav-item ">
                  <Link className="nav-link "to={'/Timer'}> 
                   Timer
                  </Link>
                  
                </li>
                <li className="nav-item">
                  <Link className="nav-link "to={'/Employee'}>Employee</Link>
               </li>
               <li className="nav-item">
                  <Link className="nav-link "to={'/Message/this harry from urlparamsdemo'}>Message</Link>
               </li>
               <li className="nav-item">
                  <Link className="nav-link "to={'/AxiosDemo'}>AxiosDemo</Link>
               </li>
               <li className="nav-item">
                  <Link className="nav-link "to={'/addemployee'}>AddEmployee</Link>
               </li>
              </ul>
            </div>
          </div>
        </nav> 
        
        
        <div>
          <Route exact path="/home" component={Home}/>
          <Route exact path="/"><Redirect to="/home" /></Route>
          <Route exact path="/Hogwarts"><Redirect to="/home" /></Route>
          <Route exact path="/characters" component={Characters}/>
            
          
          
          {/* 
          <Route exact path="/Timer" component={Timer}/>
          <Route exact path="/Employee" component={EmpDetails}/>
          <Route exact path="/Message/:msg" component={Message}/>
          <Route exact path="/login" component={FormDemo}/>
          <Route exact path="/AxiosDemo" component={AxiosDemo}/>
          <Route exact path="/addemployee" component={AddEmp}/> */}
        </div>
  

        </React.Fragment>

     </Router>
    );
  }
}

export default App;

